<template>
  <div>
    <vue-headful title="0xsp Mongoose | Dashboard" description="" />

    <!--Stats cards-->
    <div class="row">
      <div class="col-md-6 col-xl-6">
        <stats-card>
          <div class="icon-big text-center" slot="header">
            <font-awesome-icon :icon="['fas', 'flag']" />
          </div>
          <div class="numbers" slot="content">
            <p>Results Found</p>
            {{ resultNum }}
          </div>
        </stats-card>
      </div>
      <div class="col-md-6 col-xl-6">
        <stats-card>
          <div class="icon-big text-center" slot="header">
            <font-awesome-icon
              :icon="['fas', 'server']"
              :style="{ color: 'teal' }"
            />
          </div>
          <div class="numbers" slot="content">
            <p>Scanned Hosts</p>
            {{ hostsNum }}
          </div>
        </stats-card>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <card title="Hosts">
          <ul class="nav nav-tabs nav-justified">
            <li class="nav-item">
              <h5>
                <a
                  class="nav-link p-4 bg-light"
                  @click.prevent="setActive('windows')"
                  :class="{ active: isActive('windows') }"
                  href="#windows"
                  ><i class="ti-microsoft"></i> Windows</a
                >
              </h5>
            </li>
          </ul>
          <div class="tab-content py-3" id="myTabContent">
            <div
              class="tab-pane fade"
              :class="{ 'active show': isActive('windows') }"
              id="windows"
            >
              <div slot="raw-content" class="table-responsive">
                <b-table
                  id="my-table"
                  :items="windowsHosts"
                  :per-page="perPage"
                  :current-page="currentPage"
                  striped
                  hover
                  :fields="fields"
                >
                  <template v-slot:cell(actions)="row">
                    <router-link
                      class="p-2"
                      id="info"
                      :to="'categories/1/' + row.item.random_val"
                    >
                      <b-button size="sm" type="info">
                        <i class="ti-search"></i>
                      </b-button>
                    </router-link>
                    <b-button
                      id="danger"
                      @click.prevent="deleteScan(row.item)"
                      size="sm"
                      type="danger"
                    >
                      <i class="ti-trash"></i>
                    </b-button>
                  </template>
                </b-table>

                <b-pagination
                  v-model="currentPage"
                  :total-rows="rows"
                  :per-page="perPage"
                  align="center"
                  pills
                  size="lg"
                ></b-pagination>
              </div>
            </div>
          </div>
        </card>
      </div>
    </div>
  </div>
</template>
<script>
import { StatsCard } from "@/components/index";
export default {
  components: {
    StatsCard,
  },
  data() {
    return {
      windowsHosts: [],
      linuxHosts: [],
      hostsNum: null,
      resultNum: null,
      currentPage: 1,
      cmdlet:{},
      perPage: 7,
      activeItem: "windows",
      fields: [
        {
          key: "random_val",
          label: "Host Key",
          sortable: true,
          sortDirection: "desc",
        },
        { key: "sys", label: "OS", sortable: true, class: "text-center" },
        { key: "host", label: "Host", sortable: true, class: "text-center" },
        { key: "actions", label: "Actions" },
      ],
    };
  },
  methods: {
    isActive(menuItem) {
      return this.activeItem === menuItem;
    },
    setActive(menuItem) {
      this.activeItem = menuItem;
    },
    async fetchWinHosts() {
      this.axios
        .get("/mydashboard/getwindowshosts")
        .then((response) => (this.windowsHosts = response.data.windowsdata));
    },
    async fetchHostsNum() {
      this.axios
        .get("/mydashboard")
        .then(
          (response) => (
            (this.hostsNum = response.data.hosts),
            (this.resultNum = response.data.newResult)
          )
        );
    },
    deleteScan(scan) {
      this.axios.post("/mydashboard/deleteScan", scan).then((response) => {
        if (response.data.success) {
          this.$toasted.show(`${scan.random_val} Scan has been deleted!`, {
            theme: "toasted-primary",
            position: "top-center",
            duration: 1500,
            type: "success",
          });

          this.fetchWinHosts();
          this.fetchHostsNum();
        }
      });
    },
 
  },
  created() {
    this.fetchWinHosts();
    this.fetchHostsNum();
  },
  computed: {
    rows() {
          return this.windowsHosts.length;
    },
  },
};
</script>
<style>
.buttons button {
  font-size: 14px;
}
</style>
