<template>
<div>

  <nav class="navbar navbar-expand-lg navbar-light">
    <div class="container-fluid">
      <a class="navbar-brand" href="#">{{routeName}}</a>
      <button class="navbar-toggler navbar-burger"
              type="button"
              @click="toggleSidebar"
              :aria-expanded="$sidebar.showSidebar"
              aria-label="Toggle navigation">
        <span class="navbar-toggler-bar"></span>
        <span class="navbar-toggler-bar"></span>
        <span class="navbar-toggler-bar"></span>
      </button>
      <div class="collapse navbar-collapse">
        <ul class="navbar-nav mr-5 ml-auto">
          <li class="nav-item">
            <a class="nav-link" @click="openModal()" v-b-modal.commands href="#"><font-awesome-icon :icon="['fas', 'terminal']" /> Commands</a>
          </li>          
          <li class="nav-item">
          <drop-down class="nav-item"
                     title="Account"
                     title-classes="nav-link"
                     icon="ti-user">
            <a  v-b-modal.my-modal class="dropdown-item" href="#">Change Password</a>
            
            <a class="dropdown-item" @click="logout()" href="#">Logout</a>
          </drop-down>
          </li>
        </ul>
      </div>
    </div>
    </nav>
   <b-modal id="my-modal" >
     <label for="">New Password: </label>
    <input v-model="fields.password" type="password" class="form-control bg-dark text-white" placeholder="Your Password">  
      <template v-slot:modal-footer="{ ok, cancel }">
      <b-button size="md" squared @click="cancel()">
        Cancel
      </b-button>
      <b-button size="md" squared variant="primary" @click="changePassword()">
        Save
      </b-button>      
    </template>
  </b-modal>    
   <b-modal @hidden="stopTimer()" id="commands" size="xl">
     <div class="row">
      <div class="col-md-12">
        <p for="">Choose Techniques:</p>
        <cool-select
          v-model="fields.command"
          :items="cmdlet"
          item-text="title"
          item-value="command"
        />
      </div>
      <div class="col-md-12 pt-4">
          <b-form-textarea
            id="textarea"
            :value="command_output"
            v-model="command_output"
            rows="15"
            max-rows="15"
            style="background:#000;color:#fff;border-radius:10px"
          ></b-form-textarea>
      </div>
     </div>
        <template v-slot:modal-footer="{ ok, hide }">
          <b-form-input placeholder="Your Commands:" v-model="fields.command" style="background:#000;color:#fff;width: 80%;height: 43px;" type="text" /> 
         <b-button size="md" squared  @click="hide()">
          Cancel
        </b-button>
        <b-button size="md" squared variant="primary" @click="updateCommand()">
          Save & Run
        </b-button>  
      </template> 
  </b-modal>    
</div>
</template>
<script>
import { CoolSelect } from 'vue-cool-select'

export default {
  components: { CoolSelect },
  computed: {
    routeName() {
      const { name } = this.$route;
      return this.capitalizeFirstLetter(name);
    }
  },
  data() {
    return {
      activeNotifications: false,
      fields:{},
      cmdlet:{},
      commands: '',
      command_output:'',
      selected:null,
      timer:null
    };
  },
  methods: {
    capitalizeFirstLetter(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    toggleNotificationDropDown() {
      this.activeNotifications = !this.activeNotifications;
    },
    closeDropDown() {
      this.activeNotifications = false;
    },
    toggleSidebar() {
      this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
    },
    hideSidebar() {
      this.$sidebar.displaySidebar(false);
    },
    changePassword() {
      this.axios.post('/mydashboard/resetpass',this.fields).then(response => {
        if(response.data.success){
          this.$toasted.show(response.data.msg, { 
            theme: "toasted-primary", 
            position: "top-center", 
            duration : 1500,
            type:'success'
      });               
        localStorage.removeItem('x-access-token');
        this.$router.push('/login');

}else{
       this.$toasted.show(response.data.msg, { 
            theme: "toasted-primary", 
            position: "top-center", 
            duration : 1500,
            type:'error'
      });                 
        }
      });
    },
    getCommands(){
      this.axios.get('/mydashboard/commands').then(response => {
          this.fields = response.data.commands;
      });
    },    
    openModal(){
      this.timer = window.setInterval(this.getCommandOutput,3000);
    },
    getCommandOutput(){
    this.axios.get('/mydashboard/commands').then(response => {
          this.command_output = response.data.commands.command_output;
      }); 
    },
updateCommand(){
      this.axios.post('/mydashboard/updatecommand',this.fields).then(response => {
        if(response.data.success){
          this.$toasted.show(response.data.msg, { 
            theme: "toasted-primary", 
            position: "top-center", 
            duration : 1500,
            type:'success'
      });               
      }
    });
    this.getCommands();
    this.getCmdlet();
    },
    getCmdlet(){
        this.axios.get('/mydashboard/getCmdlet').then(response => {
          this.cmdlet = response.data.cmdlet.cmdlet
        });
    },
    logout(){
      localStorage.removeItem('x-access-token');
      this.$router.push('/login');
    },
    stopTimer(){
      clearInterval(this.timer)
    }
  },

  created(){
    this.getCommands();
    this.getCmdlet();
  },
};
</script>
<style>
::placeholder{
  color: #ffffff !important;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}
</style>
