import Vue from 'vue'
import VueRouter from 'vue-router'
import VueAxios from 'vue-axios'
import httpConfig from '../service/httpConfig'
import DashboardLayout from "../layout/dashboard/DashboardLayout.vue";
import Dashboard from "../views/Dashboard.vue";

import auth from '../middleware/auth';

Vue.use(VueRouter)
Vue.use(VueAxios, httpConfig)


const routes = [
  {
     path: '/login',
     name: 'Auth',
     component: () => import('../views/Auth/Login.vue'),
  },    
  {
    path: "/",
    component: DashboardLayout,
    redirect: "/dashboard",
    children: [
      {
        path: "dashboard",
        name: "dashboard",
        component: Dashboard,
        meta:{
          middleware: [auth],
        }
      },
      {
        path: '/filestorage',
        name: 'File Storage',
        component: () => import('../views/FileStorage.vue')
      },
      {
        path: '/plugins',
        name: 'Plugins',
        component: () => import('../views/Plugins.vue')
      },
      {
        path: '/categories/1/:id',
        name: 'Scan Results / Windows Categories',
        component: () => import('../views/WinScanresult.vue')
      },
      {
        path: '/categories/2/:id',
        name: 'Scan Results / Linux Categories',
        component: () => import('../views/LuxScanresult.vue')
      },
      {
        path: '/scanresult/:id/:cat',
        name: 'Scan Results',
        component: () => import('../views/Scanresult.vue')
      }              
    ]
  } 
]

